import React, { Component } from 'react';
import Datum from "../data.js";

class TodayDescriptor extends Component {
  state = {
    now: new Date()
  }

  componentDidMount() {
    this.dateUpdateInt = setInterval(() => this.setState({ now: new Date() }), 1000);
  }

  componentWillUnmount() {
    clearInterval(this.dateUpdateInt);
  }

  render() {
    return (
      <h4 className="m-3">
        {this.handleClass()}
        <div><span className="assign-badge lightblue">3</span> assignments due today</div>
      </h4>
    );
  }

  handleClass = () => {
    const date = this.state.now;
    let nextClass = this.formatClass(this.findNextClass(date))
    let nextTime = this.findNextTime(date)

    if (nextClass != null) {
      return <div className="p-b-10">{nextClass} in <strong>{nextTime}</strong></div>
    }

    return <div className="p-b-10">No Upcoming Events</div>; //TODO: GIVE A BETTER TITLE TO DESCRIBE NO SCHOOL / NO EVENTS
  }

  findNextClass = date => {
    const schedule = this.parseSchedule(this.getAlternateSchedule(date), date)
    const timeNow = date.getHours() + ":" + date.getMinutes()

    var i;
    for (i in schedule) {
      if (parseInt(timeNow.split(":")[0], 10) < parseInt(schedule[i].start.split(":")[0], 10)) {
        return schedule[i].name;
      } else if (parseInt(timeNow.split(":")[0], 10) === parseInt(schedule[i].start.split(":")[0], 10)) {
        if (parseInt(timeNow.split(":")[1], 10) <= parseInt(schedule[i].start.split(":")[1], 10)) {
          return schedule[i].name;
        }
      }
    }

    return null;
  }

  findNextTime = date => {
    const schedule = this.parseSchedule(this.getAlternateSchedule(date), date)
    const timeNow = date.getHours() + ":" + date.getMinutes()

    var i;
    for (i in schedule) {
      let differenceHours = schedule[i].start.split(":")[0] - timeNow.split(":")[0]
      let differenceMinutes = schedule[i].start.split(":")[1] - timeNow.split(":")[1]

      if (differenceMinutes < 0) {
        differenceHours -= 1;
        differenceMinutes = 60 + differenceMinutes;
      }

      let diffString = differenceHours + ":" + differenceMinutes;

      if (differenceHours >= 0) {
        return this.formatTime(diffString);
      }
    }

    return null;
  }

  formatTime = (time) => {
    let hrs = parseInt(time.split(":")[0], 10)
    let mins = parseInt(time.split(":")[1], 10)

    if (hrs === 0) { //NO HOURS
      return mins + " minute" + this.isPlural(mins);
    } else {
      if (mins === 0) { //NO MINUTES
        return hrs + " hour" + this.isPlural(hrs);
      } else {
        return hrs + " hour" + this.isPlural(hrs) + " and " + mins + " minute" + this.isPlural(mins);
      }
    }
  }

  isPlural(num) {
    if (num !== 1) {
      return "s";
    }

    return "";
  }

  formatClass = theClass => {
    if (theClass != null) {
      const classClass = "class-badge " + this.getClassColor(theClass);
      return <span className={classClass}>{theClass}</span>
    }

    return null;
  }

  getClassColor(period) {
    if (Datum.periodToColor[period] != null) {
      return Datum.periodToColor[period];
    } else {
      return Datum.periodToColor["Other"];
    }
  }

  parseSchedule = (sched, date) => {
    if (sched == null) {
      return Datum.normalSchedules[date.getDay()];
    } else {
      var splitten = sched.description.split(")");
      splitten.splice(-1,1);
      let names = splitten.map(x => x.split(" (")[0]);
      let times = splitten.map(x => x.split(" (")[1]);

      times = times.map(x => x.split("-"));

      var x;
      var convertNext = false;
      for (x in times) {
        if (convertNext) {
          times[x][0] = String(parseInt(times[x][0].split(":")[0], 10) + 12) + ":" + times[x][0].split(":")[1]
          times[x][1] = String(parseInt(times[x][1].split(":")[0], 10) + 12) + ":" + times[x][1].split(":")[1]
          continue;
        }
        if (parseInt(times[x][0].split(":")[0], 10) > parseInt(times[x][1].split(":")[0], 10)) {
          convertNext = true;
          times[x][1] = String(parseInt(times[x][1].split(":")[0], 10) + 12) + ":" + times[x][1].split(":")[1]
        }
      }

      var i;
      for (i in splitten) {
        splitten[i] = { name:names[i], start:times[i][0], end:times[i][1] };
      }
      return splitten;
    }
  }

  getAlternateSchedule = date => {
    var filteredEvents = this.filterEventsByDate(date);

    return this.findAlternate(filteredEvents);
  }

  findAlternate(events) {
    var i;
    for (i in events) {
      var x;
      for (x in events[i].summary.split(" ")) {
        if (Datum.keywords.indexOf(events[i].summary.split(" ")[x].toLowerCase()) >= 0) {
          return events[i];
        }
      }
    }

    return null;
  }

  filterEventsByDate = date => {
    var i;
    var x = 0;
    var datesEvents = [];
    for (i in this.props.appState.events) {
      let startDate = (this.props.appState.events[i].start.date || (this.props.appState.events[i].start.dateTime.split("T"))[0]).split("-");
      startDate = new Date(startDate[0],(startDate[1] - 1),startDate[2]);
      if (this.sameDay(startDate, date)) {
        datesEvents[x] = this.props.appState.events[i];
        x++;
      }
    }
    return datesEvents;
  }

  sameDay(d1, d2) {
    return d1.getFullYear() === d2.getFullYear() && d1.getMonth() === d2.getMonth() && d1.getDate() === d2.getDate();
  }

}

export default TodayDescriptor;
