import React, { Component } from 'react';

class UpcomingCard extends Component {
  state = { //GRAB ALL STATE INFORMATION FROM PARAMETERS
    completed: false
  }

  render() {
    const divClass = "upcoming-div lightblue";

    return (
      <div className="upcoming-card-div">
        <div className={divClass}>
          <p className="card-name">HW 3</p>
          <p className="card-done"><span className="badge badge-warning text-white">NOT DONE</span></p>
          <p className="card-due-class">Due <span className="class-badge purple">Period E</span></p>
          <p className="card-due-date">Wed, Oct 3</p>
        </div>
        {this.completeDiv()}
      </div>
    );
  }

  complete = () => {
    //
    //<span aria-hidden="true">&#10003;</span>

    this.setState({ completed: true });

    //COMMUNICATE CHANGE OF STATE TO APP STATE
  }

  completeDiv() {
    if (!this.state.completed) {
      return (<div className="finish-div badge-success">
        <button className="close center-exit" onClick={this.complete}>
          <img className="sched-set btn-outline-success glyph-icon" alt="finish" src="assets/si-glyph-checked.svg"/>
        </button>
      </div>);
    }

    return (<div className="finish-div badge-warning">
      <button className="close center-exit" onClick={this.complete}>
        <img className="sched-set btn-outline-warning glyph-icon" alt="finish" src="assets/si-glyph-delete.svg"/>
      </button>
    </div>);
  }

}

export default UpcomingCard;
