import React, { Component } from 'react';
import ScheduleHeader from "./components/ScheduleHeader";
import ClassView from "./components/ClassView";
import SettingsModal from "./components/SettingsModal";
import UpcomingModal from "./components/UpcomingModal";
import TodayDescriptor from "./components/TodayDescriptor";
import UpcomingView from "./components/UpcomingView";
import Datum from "./data.js";
import './App.css';

class App extends Component {
  state = {
    classNames: {
      "Period A":"Period A",
      "Period B":"Period B",
      "Period C":"Period C",
      "Period D":"Period D",
      "Period E":"Period E",
      "Period F":"Period F",
      "Period G":"Period G"
    },
    events: {},
    upcoming: [
      { name:"HW 3", classDue:"Period E", completed:false, dueDate:(new Date()) }
    ],
  };

  componentDidMount() {
    this.getEvents({
      calendarUrl: 'https://www.googleapis.com/calendar/v3/calendars/u5mgb2vlddfj70d7frf3r015h0@group.calendar.google.com/events?key=AIzaSyDtCp1eV5JlY_Smx8wNbXKngun9HZ5J9Ik',
      recurringEvents: true,
      timeMin: Datum.firstDay,
      timeMax: Datum.lastDay
    });
  }

  getEvents(settings) {
    var finalURL = settings.calendarUrl;

    if (settings.recurringEvents) {
      finalURL = finalURL + '&singleEvents=true&orderBy=starttime';
    }

    if (settings.timeMin) {
      finalURL = finalURL + '&timeMin=' + settings.timeMin;
    }

    if (settings.timeMax) {
      finalURL = finalURL + '&timeMax=' + settings.timeMax;
    }

    fetch(finalURL).then(res => {
      return res.json();
    }).then(res => {
      const data = renderList(res, settings);
      this.setState({ events : data });
    });
  }

  handleSave = settings => {
    this.setState({classNames: settings.classNames});
  }

  render() {
    return (
      <div>
        <div className="app">
          <div className="bg-image"/>
          <h2 className="name-header m-3">Hey there, Buy Geez Geez.</h2>
          <div className="today-info">
            <h3 className="m-3">It's {Datum.dayNames[(new Date()).getDay()]}, {Datum.monthNames[(new Date()).getMonth()]} {(new Date()).getDate()}</h3>
            <TodayDescriptor appState={this.state} />
          </div>
          <div className="hover-div">
            <div className="movement-div">
              <div className="schedule-panel">
                <ScheduleHeader />
                <div className="non-header">
                  <div className="switch-hr">
                    <hr className="horizontal-rule" />
                  </div>
                  <ClassView appState={this.state} />
                  <UpcomingView appState={this.state} />
                </div>
              </div>
            </div>
          </div>
        </div>
        <SettingsModal onSave={this.handleSave}/>
        <UpcomingModal />
      </div>
    );
  }
}

function comp(a, b) {
  return new Date(a.start.dateTime || a.start.date).getTime() - new Date(b.start.dateTime || b.start.date).getTime();
}

function renderList(data, settings) {
  var result = [];

  result = data.items.filter(function (item) {
    return item && item.hasOwnProperty('status') && item.status !== 'cancelled';
  }).sort(comp);

  return parseEvents(result);
}

function parseEvents(events) {
  var i;
  var newEvents = [];

  for (i in events) {
    let event = events[i];
    let newEvent = {"summary":event.summary, "description":parseDesc(event.description), "start":event.start, "end":event.end}
    newEvents[i] = newEvent;
  }

  return newEvents;
}

function parseDesc(desc) {
   if (desc !== undefined) {
     const newD = desc.replace(/<(?:.|\n)*?>/gm, '').split(',').join('').split(') ').join(')');
     return newD;
   }
   return "";
}

export default App;
